import { createElement } from '../render.js';
import { conversionDate, getDuration } from '../utils.js';


function createPointTemplate(point, offers, destinations) {
  const { basePrice, dateFrom, dateTo, destination, isFavorite, type } = point;

  // все возможные предложения для данного типа точки
  const typeOffers = offers.find((offer) => offer.type === type).offers;

  // массив предложений для данной конкретной точки
  const pointOffers = typeOffers.filter((typeOffer) => point.offers.includes(typeOffer.id));

  // направление точки
  const pointDestination = destinations.find((dest) => dest.name === destination);
  //window.console.log('typeOffers', typeOffers);
  //window.console.log('pointOffers', pointOffers);
  const selectedOffers = pointOffers.map((offer) => (
    `<li class="event__offer">
        <span class="event__offer-title">${offer.title}</span>
        &plus;&euro;&nbsp;
        <span class="event__offer-price">${offer.price}</span>
    </li>`
  ));

  return (
    `
      <li class="trip-events__item">
        <div class="event">
          <time class="event__date" datetime=${conversionDate(dateFrom, 'full-date')}>${conversionDate(dateFrom, 'short-date')}</time>
          <div class="event__type">
            <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
          </div>
          <h3 class="event__title">${type} ${pointDestination.name}</h3>
          <div class="event__schedule">
            <p class="event__time">
              <time class="event__start-time" datetime=${conversionDate(dateFrom, 'full-date-and-time')}>${conversionDate(dateFrom, 'only-time')}</time>
              &mdash;
              <time class="event__end-time" datetime=${conversionDate(dateTo, 'full-date-and-time')}>${conversionDate(dateTo, 'only-time')}</time>
            </p>
            <p class="event__duration">${getDuration(dateFrom, dateTo)}</p>
          </div>
          <p class="event__price">
            &euro;&nbsp;<span class="event__price-value">${basePrice}</span>
          </p>
          <h4 class="visually-hidden">Offers:</h4>
          <ul class="event__selected-offers">
            ${selectedOffers.join('')}
          </ul>
          <button class="event__favorite-btn ${isFavorite ? 'event__favorite-btn--active' : ''}" type="button">
            <span class="visually-hidden">Add to favorite</span>
            <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
              <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
            </svg>
          </button>
          <button class="event__rollup-btn" type="button">
            <span class="visually-hidden">Open event</span>
          </button>
        </div>
      </li>
    `
  );
}

export default class PointView {
  constructor(point, offers, destinations) {
    this.point = point;
    this.offers = offers;
    this.destinations = destinations;
  }

  getTemplate() {
    return createPointTemplate(this.point, this.offers, this.destinations);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
